/* Navigation styles */

nav {
  bottom: 0;
  position: absolute;
  background-color: white;
  left: 30%;
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
  padding: 1rem;
  display: flex;
  flex-direction: row;
  align-items: center;
}

nav > ul {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

nav > ul > li {
  list-style-type: none;
  width: auto;
  margin-right: 1rem;
}

nav > ul > li > a {
  background-color: white;
  text-decoration: none;
  color: black;
}

